.logo{
    width: 70px ;
    height: 70px;
}
.nav{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.text{
    display: flex;
    justify-content: space-evenly;
    gap: 25px;
}
.but{
    width: 150px;
    height: 40px;
    background-color: blue;
    color: white;
    font-size: 1.1rem;
    border: none;
    border-radius: 20px;
}
.but:hover{
    transition: 0.4s;
    background-color: white;
    color: blue;
}