.body{
    width: 300px;
    height: 300px;
    background-color: rgb(168,184,255);
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 50px;
    margin-bottom: 50px;
}
.body:hover{
    transition: 0.4s all;
    transform: scale(1.1);
}
.imagebox{
    width: 250px;
    height: 200px;
    transform: translateY(-20px);
}
.titlebox{
    font-size: 1.3rem;
}